import React from "react"
import { Alert } from "reactstrap"
import { MyUI } from "@macashipo/mlib"
import styled from "styled-components"
import i18next from "i18next"
export default function MUIMsgShowInUI(props) {
  const { msg } = props
  if (msg && msg.indexOf("<") == 0) {
    return <MyUI type="html" html={msg} />
  }
  return (
    <Alert color="warning" style={{ whiteSpace: "pre-line" }}>
      {msg}
    </Alert>
  )
}
