import { HUtils, MyUI } from "@macashipo/mlib"
import React from "react"
import { Alert } from "reactstrap"
import useConfigPage from "../../hooks/useConfigPage"
import "./Styles/admin_table.css"
import { fnListMyPage } from "src/helpers/fnList"
import AdminTableMsgShowInUI from "src/modules/adminpanel/MyUI/Types/admintable_msgshowinui"
export default function AdminTable(props) {
  const cfp = useConfigPage(props.configPage, {
    props,
    needOptions: true,
    needList: true,
  })
  if (cfp.showLoading) {
    return cfp.renderLoading()
  }
  if (cfp.errorMsg) {
    return cfp.renderError(cfp.errorMsg)
  }
  console.info("render Page table:", cfp)
  let _myUITypeHeader = "admintable_header"
  let _myUITypeFilter = "admintable_filter"
  if (fnListMyPage.fnGetIsInTableExpand(cfp.fnList) === true) {
    _myUITypeHeader = "admintable_header_mini"
    _myUITypeFilter = "admintable_filter_detail"
  }

  if (cfp.onePage["UI_LoadOptionError"]) {
    const errorOption = cfp.onePage["UI_LoadOptionError"]
    console.warn("error:", errorOption, Object.keys(errorOption))
    return <MyUI type="msgbox_error" />
  }

  let _chartConfig = fnListMyPage.fnGetConfigChart(cfp.fnList)

  return (
    <div className="mp-admin_table">
      {cfp.onePage._oneScreenConfig._header.show !== false && (
        <MyUI {...props} type={_myUITypeHeader} cfp={cfp} />
      )}
      <MyUI {...props} type={_myUITypeFilter} cfp={cfp} />
      {cfp.onePage._msgShowInUI && (
        <AdminTableMsgShowInUI msg={cfp.onePage._msgShowInUI} />
      )}
      {_chartConfig != null && (
        <MyUI
          {...props}
          type="admintable_chart"
          cfp={cfp}
          chartConfig={_chartConfig}
        />
      )}
      <MyUI {...props} type="admintable_table" cfp={cfp} />
    </div>
  )
}
